	module.exports = {"data":{"wordpressPost":{"title":"Peer Review Week: An analysis of peer review style and quality","content":"<p>This week celebrates the first ever <a href=\"http://exchanges.wiley.com/blog/2015/09/10/celebrating-peer-review-announcing-peer-review-week-2015/\">Peer Review Week</a>; a collaborative concept from ORCID, Wiley, Sense About Science and ScienceOpen, to highlight and celebrate the invaluable role peer review plays in scientific and medical publishing.</p>\n<p>Here at<em> BMJ Open</em> we are, of course, advocates of open peer review and as such are pleased to be publishing a timely research article by our friends at Biomed Central.</p>\n<p>The paper, from Maria Kowalczuk <em>et al</em>., <a href=\"http://bmjopen.bmj.com/content/5/9/e008707.full\">is a retrospective analysis of the quality of referee reports from author-suggested and non-author-suggested reviewers in open or single blind peer review journals.</a></p>\n<p>Their objective was to elucidate whether reviews from peers suggested by authors would show bias in quality and decision recommendation compared to reviewers selected by other means. They also aimed to assess whether open review vs. single blind review had an impact on quality and recommendation. To achieve this, the study looked at 200 reviewer reports submitted in 2010-2011 to <em>BMC Microbiology</em>, 200 submitted to <em>BMC Infectious Diseases,</em> and 400 that were submitted to the <em>Journal of Inflammation</em>, these journals use single blind peer review, open peer review and a combination of the two, due to policy change, respectively. Comparisons were made by assessing the quality of report (using the <a href=\"http://www.jclinepi.com/article/S0895-4356(99)00047-5/fulltext\">Review Quality Instrument</a>), by analysing the editorial recommendation made, and with author surveys. After statistical analysis of the data, they could conclude that the reports from reviewers suggested by the authors were of comparable quality but were more likely to suggest publication.</p>\n<p>They also conclude that the open peer review reports were of a slightly higher quality than those using single blind review. These findings are in line with those from the <a href=\"http://www.bmj.com/content/318/7175/23\">randomised trial</a> conducted by <em>The BMJ</em>, after they became one of the first journals to use open peer review in 1999, and similar to <a href=\"http://bjp.rcpsych.org/content/176/1/47\">another study by the British Journal of Psychiatry</a>.</p>\n<p>As <em>BMJ Open</em> is open access with fully open peer review, we are always happy to see further research demonstrating the success of this model.  As we use a combination of both author suggested and non-author suggested reviewers (with an in-house filtering process), we couldn&#8217;t help but agree when we spoke to the paper&#8217;s authors and they said the following,  &#8216;<em>It is reassuring that reviewers suggested by the authors provided reports of as good quality as reviewers found by editors using other means. Author-suggested reviewers tended to recommend acceptance of the manuscript more often than other reviewers, which highlights the important role of the editor in making the final decision on the manuscript&#8217;</em> .</p>\n<p>The full text of the paper, <em>Retrospective analysis of the quality of reports by author-suggested and non-author-suggested reviewers in journals operating on open or single-blind peer review models</em>&#8211; Kowalczuk <em>et al</em>., can be found here:<a href=\"http://bmjopen.bmj.com/content/5/9/e008707.full\"> http://bmjopen.bmj.com/content/5/9/e008707.full</a><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"b46bdf22-0740-5f04-8f33-b33e4c7297c8"}}

